import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import {Router, ActivatedRoute} from '@angular/router'
import { ApiService }  from '../api.service';

import { Estudante } from '../estudante';
import { Endereco } from '../endereco';
import { Mae } from '../mae';

import {EditarComponent} from '../editar/editar.component'




@Component({
  selector: 'ce-formulario',
  templateUrl: './formulario.component.html',
  styleUrls: ['./formulario.component.css']
})
export class FormularioComponent implements OnInit, AfterViewInit {

  form:FormGroup

  cep:string
  cepValido:boolean
  cpfValido:boolean

  @Input() titulo:string
  @Input() acao:string
  @Input() corBorda:string
  @Input() corBotao:string

  id:number
  estudante:Estudante
  mae:Mae
  endereco:Endereco
  url:string

  @Input() editar:EditarComponent


  constructor(private router:Router, private route:ActivatedRoute, private apiService:ApiService, private formBuilder:FormBuilder) {
    this.cepValido = false
    this.cpfValido = false
    this.cep = ''
    this.url = "/estudantes/cadastra"
  }

  ngAfterViewInit(){

}
  ngOnInit() {
    this.id = +this.route.snapshot.paramMap.get('id');

    this.form = this.formBuilder.group({
      nome: this.formBuilder.control('', [Validators.required]),
      nascimento: this.formBuilder.control('', [Validators.required]),
      serie_ingresso: this.formBuilder.control('', [Validators.required]),
      nomeMae: this.formBuilder.control('', [Validators.required]),
      cpf: this.formBuilder.control('', [Validators.required]),
      data_pagamento: this.formBuilder.control('', [Validators.required]),
      rua: this.formBuilder.control('', [Validators.required]),
      numero: this.formBuilder.control('', [Validators.required]),
      complemento: this.formBuilder.control(''),
      bairro: this.formBuilder.control('', [Validators.required]),
      cep: this.formBuilder.control('', [Validators.required]),
      cidade: this.formBuilder.control('', [Validators.required]),
      estado: this.formBuilder.control('', [Validators.required])
    })

    if(this.acao == "Salvar"){
    this.url = `/estudantes/update/${this.id}`

    this.apiService.getEstudanteData(this.id)
      .subscribe(response =>{
        this.estudante = response

        this.apiService.getEnderecoData(this.id)
          .subscribe(response =>{
            this.endereco = response

            this.apiService.getMaeData(this.id)
              .subscribe(response =>{
                this.mae = response

      this.form.setValue({nome: `${this.estudante[0]['nome']}`,
      nascimento: `${this.estudante[0]['nascimento']}`,
      serie_ingresso: `${this.estudante[0]['serie_ingresso']}`,
      nomeMae: `${this.mae[0]['nome']}`,
      cpf: `${this.mae[0]['cpf']}`,
      data_pagamento: `${this.mae[0]['data_pagamento']}`,
      rua: `${this.endereco[0]['rua']}`,
      numero: `${this.endereco[0]['numero']}`,
      complemento: `${this.endereco[0]['complemento']}`,
      bairro: `${this.endereco[0]['bairro']}`,
      cep: `${this.endereco[0]['cep']}`,
      cidade: `${this.endereco[0]['cidade']}`,
      estado:`${this.endereco[0]['estado']}`

      })
    })
  })
})

}

    this.onChangesCpf()
    this.onChangesCep()


  }

  defineValoresFormulario(estado:string, cidade:string){
    this.form.setValue({nome: `${this.form.get('nome').value}`,
    nascimento: `${this.form.get('nascimento').value}`,
    serie_ingresso: `${this.form.get('serie_ingresso').value}`,
    nomeMae: `${this.form.get('nomeMae').value}`,
    cpf: `${this.form.get('cpf').value}`,
    data_pagamento: `${this.form.get('data_pagamento').value}`,
    rua: `${this.form.get('rua').value}`,
    numero: `${this.form.get('numero').value}`,
    complemento: `${this.form.get('complemento').value}`,
    bairro: `${this.form.get('bairro').value}`,
    cep: `${this.form.get('cep').value}`,
    cidade: cidade,
    estado: estado

    })
  }

  onChangesCep() :void{
   this.form.get('cep').valueChanges.subscribe(val => {

     if(`${val}`.length < 8 && this.cepValido == true){
       this.cepValido = false;
       this.defineValoresFormulario('','')
     }
     else if(`${val}`.length == 8 && this.cepValido == false){
       this.verificaCep(`${val}`)
    }

    })
  }
  public verificaCep(cepIn:string):void{

    this.apiService.getCep(cepIn).subscribe(cep => {
      if(cep == "vazio" || cep['erro'] == true){
        this.cepValido = false
        return;
      }
      this.cepValido = true
      this.defineValoresFormulario(cep['uf'],cep['localidade'])
    })
  }

  onChangesCpf() :void{
    this.form.get('cpf').valueChanges.subscribe(val => {
      this.verificaCpf(`${val}`)
    })
  }


  public verificaCpf(cpfIn: string): boolean {

        let cpf:string
        cpf =  cpfIn;
        if (cpf == null) {
            this.cpfValido = false;
             return false;
        }
        if (cpf.length != 11) {
            this.cpfValido = false;
            return false;
        }
        if ((cpf == '00000000000') || (cpf == '11111111111') || (cpf == '22222222222') || (cpf == '33333333333') || (cpf == '44444444444') || (cpf == '55555555555') || (cpf == '66666666666') || (cpf == '77777777777') || (cpf == '88888888888') || (cpf == '99999999999')) {
            return false;
        }
        let numero: number = 0;
        let caracter: string = '';
        let numeros: string = '0123456789';
        let j: number = 10;
        let somatorio: number = 0;
        let resto: number = 0;
        let digito1: number = 0;
        let digito2: number = 0;
        let cpfAux: string = '';
        cpfAux = cpf.substring(0, 9);
        for (let i: number = 0; i < 9; i++) {
            caracter = cpfAux.charAt(i);
            if (numeros.search(caracter) == -1) {
                this.cpfValido = false;
                return false;
            }
            numero = Number(caracter);
            somatorio = somatorio + (numero * j);
            j--;
        }
        resto = somatorio % 11;
        digito1 = 11 - resto;
        if (digito1 > 9) {
            digito1 = 0;
        }
        j = 11;
        somatorio = 0;
        cpfAux = cpfAux + digito1;
        for (let i: number = 0; i < 10; i++) {
            caracter = cpfAux.charAt(i);
            numero = Number(caracter);
            somatorio = somatorio + (numero * j);
            j--;
        }
        resto = somatorio % 11;
        digito2 = 11 - resto;
        if (digito2 > 9) {
            digito2 = 0;
        }
        cpfAux = cpfAux + digito2;
        if (cpf != cpfAux) {
            this.cpfValido = false;
            return false;
        }
        else {
            this.cpfValido = true;
            return true;
        }
    }

    onSubmit() {

      this.apiService.enviaForm(this.form, this.url)
      .subscribe(() => {

      this.cep = ''
      this.router.navigate(['/alunos'])
      })

  }


}
