import { Injectable } from '@angular/core';

import { Estudante } from './estudante';
import { Endereco } from './endereco';
import { Mae } from './mae';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators'
import { FormGroup } from '@angular/forms';
import {Http, Headers, RequestOptions} from '@angular/http'

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  estudantes:Estudante



  constructor(private http:Http) {

  }

  async getEstudantes():Promise<Array<Estudante>>{
      const response = await this.http.get('/estudantes')
      .toPromise();
    return response.json();
  }



  async delete(id:number):Promise<string>{
      const response = await this.http.get('/estudantes/delete/'+id)
      .toPromise();
    return(response.json())
  }


  getCep(cep:string):Observable<string>{
        return this.http.get('/cep/'+cep)
        .pipe(map(response => response.json()));
  }

  enviaForm(form: FormGroup, enderecoApi:string):Observable<string> {
    const headers = new Headers()
    headers.append('Content-Type', 'application/json')
    return this.http.post(enderecoApi,
                          form.value,
                          new RequestOptions({headers: headers}))
                    .pipe(map(response => response.toString()));

  }
  getEstudanteData(id:number):Observable<Estudante>{
    const headers = new Headers()
    headers.append('Content-Type', 'application/json')
    return  this.http.get('/estudantes/estudante/'+id)
      .pipe(map(response => response.json()));

  }

  getEnderecoData(id:number):Observable<Endereco>{
    const headers = new Headers()
    headers.append('Content-Type', 'application/json')
    return  this.http.get('/estudantes/endereco/'+id)
      .pipe(map(response => response.json()));

  }

  getMaeData(id:number):Observable<Mae>{
    const headers = new Headers()
    headers.append('Content-Type', 'application/json')
    return  this.http.get('/estudantes/mae/'+id)
      .pipe(map(response => response.json()));
  }

}
