import { Component, OnInit, Input, Inject } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Http } from '@angular/http';

@Component({
  selector: 'ce-modal-focus',
  templateUrl: './modal-focus.component.html',
  styleUrls: ['./modal-focus.component.css']
})
export class ModalFocusComponent implements OnInit {


  constructor() { }

  ngOnInit() {
  }

}

@Component({
  selector: 'ngbd-modal-confirm',
  template: `
  <div class="modal-header">
    <h4 class="modal-title" id="modal-title">Deletar aluno</h4>
    <button type="button" class="close" aria-describedby="modal-title" (click)="modal.dismiss('Cross click')">
      <span aria-hidden="true">&times;</span>
    </button>
  </div>
  <div class="modal-body">
    <p><strong>Você tem certeza que deseja exluir o(a) aluno(a) {{nome}}?</strong></p>
    <p><span class="text-danger">Esta operação não poderá ser desfeita.</span></p>
  </div>
  <div class="modal-footer">
    <button type="button" class="btn btn-outline-secondary" (click)="modal.dismiss('cancel click')">Cancelar</button>
    <button type="button" class="btn btn-danger" (click)="deleta()">Sim</button>
  </div>
  `
})
export class NgbdModalConfirm {

  constructor(public modal: NgbActiveModal, @Inject("windowObject") window: Window) {}
  @Input('id') static id:string
  resposta:string


  deleta(){
    window.location.href = "/estudantes/delete/"+NgbdModalConfirm.id

    this.modal.close('Ok click')

  }
}

@Component({
  selector: 'ngbd-modal-confirm-autofocus',
  template: `
  <div class="modal-header">
    <h4 class="modal-title" id="modal-title">Profile deletion</h4>
    <button type="button" class="close" aria-label="Close button" aria-describedby="modal-title" (click)="modal.dismiss('Cross click')">
      <span aria-hidden="true">&times;</span>
    </button>
  </div>
  <div class="modal-body">
    <p><strong>Are you sure you want to delete <span class="text-primary">"John Doe"</span> profile?</strong></p>
    <p>All information associated to this user profile will be permanently deleted.
    <span class="text-danger">This operation can not be undone.</span>
    </p>
  </div>
  <div class="modal-footer">
    <button type="button" class="btn btn-outline-secondary" (click)="modal.dismiss('cancel click')">Cancel</button>
    <button type="button" ngbAutofocus class="btn btn-danger" (click)="modal.close('Ok click')">Ok</button>
  </div>
  `
})
export class NgbdModalConfirmAutofocus {
  constructor(public modal: NgbActiveModal) {}
}

const MODALS = {
  focusFirst: NgbdModalConfirm,
  autofocus: NgbdModalConfirmAutofocus
};

@Component({
  selector: 'ce-ngbd-modal-focus',
  templateUrl: '././modal-focus.component.html'
})
export class NgbdModalFocus {
  withAutofocus = `<button type="button" ngbAutofocus class="btn btn-danger"
      (click)="modal.close('Ok click')">Ok</button>`;



  constructor(private _modalService: NgbModal) {}

 @Input('id')  id:string

  open(name: string) {
    NgbdModalConfirm.id = this.id
    this._modalService.open(MODALS[name]);

  }
}
